.banner-section {
  background: url(banner) no-repeat center / cover;
  min-height: 600px;
  color: $white;
  position: relative;
  overflow: hidden;

  .container {
    min-height: 600px;
  }

  &__video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 140%;
    height: 140%;
    transform: translate(-50%, -50%);

  }

  &__opacity {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
  }

  &__content {
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    width: 60%;
    left: 5%;
  }

  &__icon {
    img {
      width: 180px;
    }
  }

  &__text {
    font-size: 37px;
    text-transform: uppercase;
    span {
      font-size: 20px;
      vertical-align: top;
      .small {
          font-size: 10px;
      }
    }
  }

  &__buttons {
    position: absolute;
    bottom: 5%;
    left: 5%;
    display: flex;
    flex-flow: row nowrap;
  }

  &__button {
    text-transform: uppercase;
    background: $white;
    color: $black;
    text-decoration: none;
    padding: 18px 30px;
    cursor: pointer;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 1px;
    transition: background .2s ease;

    &:hover {
      background: $gray;
    }

    &:not(:first-child) {
      margin-left: 20px;
      background: none;
      color: $white;
    }
  }
}

.small-banner {
  background: url(banner) no-repeat center / cover;
  position: relative;
  min-height: 350px;

  &__opacity {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
  }

  &__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $white;
    font-size: 40px;
    text-align: center;
  }
}

@media only screen and (max-width : 1024px) {
  .small-banner {
    &__text {
      width: 70%;
    }
  }

  .banner-section {
    &_mobile {
      display: block;
    }

    &__content {
      width: 70%;
    }

    &__video {
      display: none;
    }
  }
}

@media only screen and (max-width : 767px) {

  .banner-section {
    height: 100vh;

    &__video {
      display: none;
    }

    &__content {
      width: 100%;
      left: 0;
      padding: 0 10px;
      text-align: center;
      top: 40%;
      transform: translateY(-50%);
    }

    &__text {
      font-size: 26px;
      letter-spacing: .5px;

      span {
        font-size: 14px;
      }
    }

    &__buttons {
      flex-flow: column nowrap;
      width: 70%;
      left: 50%;
      transform: translateX(-50%);
    }

    &__button {
      text-align: center;

      &:not(:first-child) {
        margin-left: 0;
        margin-top: 15px;
      }
    }
  }

  .small-banner {
    margin-top: 42px;

    &__text {
      width: 80%;
      font-size: 22px;
      line-height: 28px;
    }
  }

}
