.quickshop {
  padding: 15px 0 50px 0;

  &__header {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__button {
    background: $black;
    color: $white;
    text-decoration: none;
    padding: 20px 45px;
    cursor: pointer;
    letter-spacing: 1px;
    font-size: 14px;
    transition: background .2s ease;
    display: inline-block;

    &:hover {
      background: rgba(0,0,0, 0.6);
    }
  }

  &__title {
    font-size: 30px;
    line-height: 30px;

    span {
      font-size: 16px;
      vertical-align: top;
      line-height: 22px;
    }
  }

  &__inner {
    display: flex;
    flex-flow: row wrap;
    margin-top: 50px;
    position: relative;
  }

  &__item {
    width: 25%;
    padding: 0 3%;
  }

  &__footer {
    margin-top: 30px;
    text-align: center;
    display: none;
  }

  .slick-arrow {
    color: transparent;
    background: transparent;
    border: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0;
    padding: 0;
    width: 35px;
    height: 70px;
    z-index: 15;
    transition: opacity .2s ease;
    opacity: 1;
  }

  .slick-next {
    right: 0;
    background: url(next) no-repeat center / contain;
  }
  .slick-prev {
    left: 0;
    background: url(back) no-repeat center / contain;
  }

  .slick-disabled {
    opacity: .3;
  }

}

.quickshop-item {
  text-align: center;
  cursor: pointer;

  &__content {
    transform: translate3d(0, 50px, 0);
    transition: transform .2s ease;
    will-change: transform;
  }

  &__inner {
    background: $light_gray;
    color: $black;
    padding: 20px;
    position: relative;
    transition: background .2s ease, color .2s ease;
    will-change: background, color;
    min-height: 365px;
  }

  &__title {
    font-size: 20px;
    text-transform: uppercase;
    line-height: 25px;
    letter-spacing: .5px;
  }

  &__description {
    text-transform: uppercase;
    font-size: 16px;
    margin-top: 20px;
    line-height: 20px;
    letter-spacing: .5px;
    p {
      span {
        font-size: 10px;
        vertical-align: top;
        line-height: 16px;
      }
    }


  }

  &__image {
    min-height: 100px;

    img {
      display: block;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 50px);
      width: 120%;
    }
  }

  &__footer {
    margin-top: 80px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
  }

  &__info {
    font-size: 20px;
    line-height: 20px;
    padding: 0 10px;

    &:not(:first-child) {
      border-left: 2px solid $black;
    }

    span {
      font-size: 14px;
      vertical-align: top;
      line-height: 10px;
      padding: 0 2px;
    }
  }

  &__buttons {
    margin-top: 20px;
    opacity: 0;
    transition: opacity .2s ease;
    will-change: opacity;
  }

  &__button {
    background: $black;
    color: $white;
    margin-top: 10px;
    cursor: pointer;
    padding: 10px 5px;
    font-size: 14px;
    letter-spacing: 1px;
    transition: background .2s ease;

    &:hover {
      background: $gray;
    }
  }

  &:hover {
    .quickshop-item {
      &__content {
        transform: translate3d(0, 0, 0);
      }

      &__inner {
        background: $light_red;
        color: $white;
      }

      &__buttons {
        opacity: 1;
      }
    }
  }
}

@media only screen and (max-width : 1024px) {
  .quickshop {
    &__inner {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }

  .quickshop-item {
    width: 40%;
  }
}

@media only screen and (max-width : 767px) {

  .quickshop {
    padding-top: 0;

    &__header > &__button {
      display: none;
    }

    &__title {
      margin: 0 auto;
      font-size: 18px;
      line-height: 18px;

      span {
        font-size: 12px;
        line-height: 13px;
      }
    }

    &__inner {
      margin-top: 30px;
    }

    &__item {
      padding: 0 5%;
    }

    &__footer {
      display: block;
    }
  }

  .quickshop-item {
    position: relative;

    &__content {
      transform: translate3d(0, 0, 0);
      transition: opacity .2s ease;
    }

    &__buttons {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 85%;
      margin-top: 0;
    }

    &:hover {
      .quickshop-item__buttons {
        opacity: 1;
      }

      .quickshop-item__content {
        opacity: .4;
      }
    }

    &__title {
      font-size: 16px;
      line-height: 20px;
    }

    &__description {
      font-size: 14px;
      line-height: 18px;

      p span {
        font-size: 8px;
        line-height: 16px;
      }
    }
  }

}
