.footer {
  background: $light_gray;
  padding: 25px 0;

  &__top {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  &__links-inner {
    display: flex;
    flex-flow: row nowrap;
    padding-right: 40px;
  }

  &__links-item {
    &:not(:first-child) {
      margin-left: 30px;
    }
  }

  &__item_large {
    flex: 1 1 auto;
    padding: 0 4%;
  }

  &__link {
    display: block;
    color: $black;
    text-decoration: none;
    font-size: 14px;

    &:not(:first-child) {
      margin-top: 3px;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &__heading {
    border-bottom: 2px solid $black;
    font-size: 14px;
    padding-bottom: 2px;
    margin-bottom: 5px;
  }


  &__contacts {
    display: flex;
    flex-flow: row nowrap;
    font-size: 14px;
  }

  &__contact-item {
    text-decoration: none;
    color: $black;

    &:hover {
      color: rgba(0,0,0,0.8);
    }
    &:not(:first-child) {
      margin-left: 8%;
    }
  }

  &__map-link {
    margin-top: 15px;
    display: block;
    color: $black;
  }

  &__title {
    font-size: 14px;
  }

  &__subscribe {
    max-width: 300px;
  }

  &__phone {
    color: $black;
    text-decoration: none;
    display: block;

    &:hover {
      color: rgba(0,0,0,0.8);
    }
    &::before {
      content: 'T:';
      display: inline-block;
      margin-right: 10px;
    }
  }

  &__email {
    color: $black;
    text-decoration: none;
    display: block;
    &:hover {
      color: rgba(0,0,0,0.8);
    }
    &::before {
      content: 'E:';
      display: inline-block;
      margin-right: 10px;
    }
  }

  &__contact {
    &:not(:first-child) {
      margin-top: 15px;
    }
  }

  &__input {
    display: flex;
    flex-flow: row nowrap;
    margin-top: 15px;
  }

  &__input-field {
    padding: 20px;
    border: none;
    background: $white;
    width: 80%;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none;
    font-size: 16px;
    font-weight: lighter;
  }

  &__submit {
    width: 20%;
    border: none;
    background: $black;
    color: $white;
  }

  &__social {
    margin-top: 15px;
  }

  &__social-link {
    text-decoration: none;
    color: $black;
    width: 20px;
    height: 20px;
    display: inline-block;
    cursor: pointer;
    opacity: .7;
    transition: opacity .2s ease;

    &:not(:first-child) {
      margin-left: 20px;
    }

    &:hover {
      opacity: 1;
    }

    &_instagram {
      background: url(instagram_icon) no-repeat center / contain;
    }

    &_facebook {
      background: url(facebook_icon) no-repeat center / contain;
    }

    &_twitter {
      background: url(twitter_icon) no-repeat center / contain;
    }

    &_pinterest {
      background: url(pinterest_icon) no-repeat center / contain;
    }
  }

  &__middle {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  &__logo {
    max-width: 70px;

    img {
      width: 100%;
    }
  }

  &__logo-text {
    margin-left: 20px;

    div:first-child {
      font-size: 16px;
      line-height: 16px;

      span {
        font-size: 10px;
        vertical-align: top;
        line-height: 8px;
      }
    }

    a {
      color: $black;
      text-decoration: none;
    }
    span {
      font-size: 14px;
      vertical-align: top;
      line-height: 10px;
      padding: 0 2px;
    }
  }

  &__buttons {
    margin-top: 20px;
    opacity: 0;
    transition: opacity .2s ease;
    will-change: opacity;
  }

  &__button {
    background: $black;
    color: $white;
    margin-top: 10px;
    cursor: pointer;
    padding: 10px 5px;
    font-size: 14px;
    letter-spacing: 1px;
    transition: opacity .2s ease;

    &:hover {
      opacity: 0.4;
    }
  }

  &:hover {
    .quickshop-item {
      &__content {
        transform: translate3d(0, 0, 0);
      }

      &__inner {
        background: $light_red;
        color: $white;
      }
    }
  }

  &__bottom {
    margin-top: 20px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    a {
      color: $black;
      text-decoration: none;
      padding: 0 10px;
      font-size: 14px;

      &:first-child {
        padding-left: 0;
      }

      &:not(:first-child) {
        border-left: 1px solid $black;
      }

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

@media only screen and (max-width : 1236px) {

  .footer {
    &__contacts {
      font-size: 10px;
    }
  }
}

@media only screen and (max-width : 1024px) {
  .footer {

    &__top {
      flex-flow: column-reverse nowrap;
    }

    &__subscribe {
      max-width: initial;
    }

    &__title {
      max-width: 300px;
    }

    &__item {
      padding: 0;
      margin-top: 30px;
    }

    &__contacts {
      flex-flow: column nowrap;
    }

    &__contact-item {
      margin-left: 0 !important;

      &:not(:first-child) {
        margin-top: 30px;
      }
    }

    &__links-inner {
      margin-top: 30px;
    }

    &__middle {
      margin-top: 30px;
    }

    &__logo {
      max-width: 45px;
    }

    &__logo-text {
      div:last-child {
        font-size: 14px;
      }
    }

    &__bottom {
      a {
        font-size: 12px;
        padding: 0 5px;
      }
    }
  }
}
