.coffee-section {
  background: $light_gray;
  padding: 40px 0 110px 0;

  &__title {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
  }

  &__number {
    font-size: 80px;
  }

  &__text {
    font-size: 30px;
    line-height: 40px;
    margin-left: 10px;

    span {
      font-size: 16px;
      vertical-align: top;
      line-height: 30px;
    }
  }

  &__inner {
    margin-top: 60px;
    display: flex;
    flex-flow: row nowrap;
  }

  &__item {
    width: 24%;
    cursor: pointer;

    &:not(:first-child) {
      margin-left: 1.5%;
    }

    &_active {
      .coffee-item__image {
        svg path{
          fill: $red;
        }
      }
      .coffee-item__title {
        color: $red;


        &::before {
          background: $red;
        }
      }

      .coffee-item__buy button{
        background: $red;
      }
    }

    &:hover {
      .coffee-item__image {
        svg path{
          fill: $red;
        }
      }
      .coffee-item__title {
        color: $red;

        &::before {
          background: $red;
        }
      }

      .coffee-item__review {
        display: flex;
      }

      .coffee-item__buy button {
        background: $red;
      }
    }
  }
}

.coffee-item {
  text-align: center;
  position: relative;

  &__strength {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    margin-top: 10px;

    &-title {
      padding: 5px 0;
    }
  }

  &__image {
    svg, img {
      width: 100%;
      max-height: 200px;

      path {
        transition: fill .2s ease;
      }
    }
  }

  &__title {
    text-transform: uppercase;
    position: relative;
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 1px;
    color: $black;
    transition: color .2s ease;
    margin-top: 30px;

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 70%;
      height: 1px;
      background: $black;
      transition: background .2s ease;
    }
  }

  &__checkboxes {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin-left: 10px;
    padding-bottom: 5px;

    input {
      display: none;
    }
  }

  &__checkbox {
    margin-left: 5px;

    label {
      line-height: 0;
      border-radius: 50%;
      font-size: 12px;
      user-select: none;
      cursor: pointer;
      transition: background .2s ease, color .2s ease;
      width: 20px;
      height: 20px;
      display: block;
      position: relative;

      span {
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    input:checked ~ label {
      background: $black;
      color: $white;
    }
  }

  &__price {
    margin-top: 10px;
    letter-spacing: .5px;
    font-size: 14px;
    line-height: 16px;

    span {
      font-size: 8px;
      vertical-align: top;
      line-height: 13px;

    }
  }

  &__review {
    position: absolute;
    bottom: -20px;
    transform: translateY(100%);
    width: 200%;
    background: $white;
    display: none;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    padding: 10px 10px;
    z-index: 10;

    &::before {
      position: absolute;
      content: '';
      top: 0;
      transform: rotate(45deg) translateY(-50%);
      width: 20px;
      height: 20px;
      background: $white;
      left: 20%;
      z-index: 5;
    }
  }

  &__avatar {
    width: 40px;
    min-width: 40px;
    height: 40px;
    background: $gray;
    margin-right: 15px;
  }

  &__buy {
    margin-top: 20px;

    button {
      background: $black;
      border: none;
      color: $white;
      text-transform: uppercase;
      padding: 10px 20px;
      font-size: 12px;
      transition: background .2s ease;
    }
  }

  &:nth-child(2) {
    .coffee-item__review::before {
      left: 20%;
    }
  }

  &:nth-child(3) {
    .coffee-item__review::before {
      left: 20%;
    }
  }

  &:nth-child(4) {
    .coffee-item__review {
      right: 0;

      &::before {
        right: 25%;
        left: initial;
      }
    }
  }

  &:hover {
    svg {
      g {
        fill: $red;
      }
    }
  }
}

.tab-links {
  background: $light_gray;

  &__buttons {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }

  &__button {
    background: $white;
    text-decoration: none;
    padding: 20px 10px;
    color: $gray;
    cursor: pointer;
    font-size: 16px;
    letter-spacing: 1px;
    min-width: 150px;
    text-align: center;
    font-weight: 300;
    transition: background .2s ease, color .2s ease;

    &:not(:first-child) {
      margin-left: 15px;
    }

    &_active,
    &:hover {
      background: $red;
      color: $white;
    }
  }
}

.subscribe-step {
  background: $light_gray;
  margin-top: 15px;
  padding: 30px 0;

  p {
    margin: 0;
  }

  &_last {
    margin-bottom: 15px;
  }

  &__title {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    justify-content: center;
  }

  &__subtext {
    display: block;
    text-align: center;
    font-size: 12px;
    margin-top: 20px;
  }

  &__number {
    font-size: 80px;
  }

  &__text {
    font-size: 30px;
    line-height: 58px;
    margin-left: 10px;

    span {
      font-size: 16px;
      vertical-align: top;
      line-height: 45px;
    }
  }

  &__inner {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    margin-top: 60px;
  }

  &__item {
    width: 33.3%;
  }

  &__buttons {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    margin-top: 30px;
  }

  &__button {
    background: $red;
    color: $white;
    width: 40%;
    padding: 20px 10px;
    cursor: pointer;
    text-align: center;
    letter-spacing: 1px;
    font-size: 16px;
    font-weight: 300;
    text-decoration: none;

    &:not(:first-child) {
      margin-left: 15px;
    }

    span {
      font-size: 10px;
      vertical-align: top;
    }
  }
}

.subscribe-item {
  position: relative;
  padding: 0 5%;
  cursor: pointer;

  img {
    width: 100%;
  }

  &__footer {
    margin-top: 40px;
    text-align: center;
    color: $black;
    transition: color .2s ease;
  }

  &__label {
    text-transform: uppercase;
    font-size: 18px;
  }

  &__info {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }

  &__price,
  &__quantity {
    padding: 0 10px;
    font-size: 16px;
    line-height: 14px;

    span {
      font-size: 10px;
      vertical-align: top;
      line-height: 8px;
    }
  }

  &__price {
    display: flex;

    .js-price-per-box {
      margin-left: 3px;
    }
  }

  &__quantity {
    border-right: 2px solid $black;
    transition: border-color .2s ease;
  }

  &__sticker {
    position: absolute;
    right: 0;
    background: $black;
    color: $white;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    text-align: center;
    top: 35%;
    z-index: 10;
    transform: translateY(-50%);
  }

  &__sticker-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 14px;
  }

  &__image {
    opacity: .2;
    transition: opacity .2s ease;
    will-change: opacity;
  }

  &:hover {
    .subscribe-item__footer {
      color: $red;
    }

    .subscribe-item__image {
      opacity: 1;
    }

    .subscribe-item__quantity {
      border-color: $red;
    }
  }

  &._active {
    .subscribe-item__footer {
      color: $red;
    }

    .subscribe-item__image {
      opacity: 1;
    }

    .subscribe-item__quantity {
      border-color: $red;
    }
  }
}

@media only screen and (max-width : 1024px) {

  .coffee-section {
    padding-bottom: 40px;

    &__inner {
      flex-wrap: wrap;
      justify-content: space-around;
    }

    &__item {
      width: 40%;

      &:nth-child(n+3) {
        margin-top: 50px;
      }

      &:not(:first-child) {
        margin-left: 0;
      }
    }
  }

  .coffee-item {
    &__review {
      display: none !important;
    }
  }

}

@media only screen and (max-width : 767px) {

  .coffee-section {
    padding-bottom: 50px;

    &__title {
      max-width: 300px;
    }

    &__number {
      font-size: 35px;
      line-height: 35px;
    }

    &__text {
      font-size: 14px;
      line-height: 18px;

      span {
        font-size: 9px;
        line-height: 14px;
      }
    }

    &__inner {
      flex-wrap: wrap;
      margin-top: 0;
    }

    &__item {
      width: 100%;
      max-width: 300px;
      margin: 50px auto 0 auto;

      &:not(:first-child) {
        margin-left: auto;
      }
    }
  }

  .tab-links {
    padding-top: 60px;

    &__button {
      min-width: 45%;
    }
  }

  .coffee-item {
    &__image {
      img {
        width: 80%;
      }
    }
  }

  .coffee-item {
    &:hover .coffee-item__review {
      display: none;
    }
  }

  .subscribe-step {
    &__title {
      max-width: 200px;
      margin: 0 auto;
    }
    &__number {
      font-size: 35px;
      line-height: 35px;
    }

    &__text {
      font-size: 14px;
      line-height: 18px;

      span {
        font-size: 9px;
        line-height: 14px;
      }
    }

    &__inner {
      flex-flow: column nowrap;
      align-items: center;
      margin-top: 0;
    }

    &__buttons {
      flex-flow: column nowrap;
      align-items: center;
    }

    &__button {
      width: 80%;
      font-size: 12px;

      &:not(:first-child) {
        margin-left: 0;
        margin-top: 15px;
      }
    }
  }

  .subscribe-item {
    width: 80%;
    margin-top: 50px;
  }
}

.hidden-buttons {
  display: none;
}
