.about-section, .about-text-section  {
  padding: 10px 0;
  .container {
    display: flex;
    flex-flow: row nowrap;
  }
}

.about-item, .about-item-text {
  width: 32.3%;
  text-align: center;
  min-height: 300px;
  position: relative;

  &-text {
    min-height: 40px;
  }

  &:not(:first-child) {
    margin-left: 1.5%;
  }

  &__header {
    font-size: 24px;
    padding: 20px 0;
    text-transform: uppercase;
  }

  &__image {
    background: no-repeat center / cover;
    height: 70%;

  }

  &__text {
    background: $white;
    color: $black;
    position: absolute;
    width: 75%;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    text-transform: uppercase;
    font-size: 16px;
    line-height: 20px;
    padding: 20px 45px;
    height: 122px;
  }
}

@media only screen and (max-width : 767px) {

  .about-text-section {
    .container {
      flex-flow: column nowrap;
    }
  }

  .about-item-text {
    width: 100%;


    &:not(:first-child) {
      margin: 0;
    }
  }

  .about-item__header {
    font-size: 18px;
  }

  .about-section {
    padding-top: 0;

    .container {
      flex-wrap: wrap;
      padding: 0;
    }

    &__item {
      width: 100%;
      min-height: 240px;
    }
  }

  .about-item {
    &:not(:first-child) {
      margin: 0;
    }

    &__text {
      height: 105px;
      padding: 15px;
      font-size: 14px;
    }

    &__image {
      height: 70%;
      min-height: 171px;
    }
  }

}

@media only screen and (max-width : 1024px) {
  .about-item-text {
    min-height: 1px;
    padding: 15px 0;

    .about-item__header {
      font-size: 16px;
      padding: 0 20px;
    }
  }

  .about-item {
    min-height: 245px;

    &__text {
      width: 90%;
      bottom: 4%;
      font-size: 14px;
      padding: 20px;
    }
  }

  .about-section {
    padding: 30px 0 15px 0;
  }
}
