.shop {
  padding: 30px 0;

  .container {
    display: flex;
    flex-flow: row wrap;
  }
}

.product-view {
  background: $light_gray;
  padding: 30px 0;
  margin-bottom: 30px;
  display: block;

  &._hidden {
    display: none;
  }

  &__close {
    position: absolute;
    bottom: 0;
    right: 30px;
    cursor: pointer;
  }

  &__close-text,
  &__close-icon {
    display: inline-block;
    font-size: 20px;
    line-height: 40px;
    font-weight: 300;
  }

  &__close-icon {
    margin-left: 10px;
  }

  &__close-text {
    color: $gray;
  }

  &__inner {
    display: flex;
    flex-flow: row nowrap;
  }

  &__row {
    width: 31.3%;

    &:not(:first-child) {
      margin-left: 3.3%;
    }
  }

  &__image {

    img {
      width: 100%;
    }
  }

  &__footer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    margin-top: 20px;
  }

  &__info {
    font-size: 20px;
    line-height: 20px;
    padding: 0 10px;

    &:not(:first-child) {
      border-left: 2px solid $black;
    }

    span {
      font-size: 14px;
      vertical-align: top;
      line-height: 10px;
      padding: 0 2px;
    }
  }

  &__title {
    font-size: 28px;
    max-width: 300px;
    text-transform: uppercase;
  }

  &__consist {
    font-size: 20px;
    max-width: 350px;
    margin-top: 20px;
    text-transform: uppercase;

    span {
      font-size: 12px;
      vertical-align: top;
    }
  }

  &__label {
    font-size: 20px;
    margin-top: 20px;
    text-transform: uppercase;
  }

  &__description {
    margin-top: 40px;
    font-size: 16px;
  }

  &__actions {
    display: flex;
    flex-flow: row wrap;
    align-content: flex-start;
  }

  &__action-item {
    width: 48%;

    &:nth-child(n+3) {
      margin-top: 10px;
    }

    &:nth-child(2n) {
      margin-left: 10px;
    }
  }

  &__button {
    color: $white;
    background: $black;
    padding: 20px;
    text-align: center;
    cursor: pointer;
    transition: background .2s ease, color .2s ease;
  }

}

.dropdown {
  position: relative;
  background: $white;
  padding: 20px;
  text-align: center;
  cursor: pointer;

  &__header {
    font-size: 16px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 5%;
      transform: translateY(-80%) rotate(-45deg);
      width: 10px;
      height: 10px;
      border-left: 1px solid $gray;
      border-bottom: 1px solid $gray;
    }
  }

  &__container {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    transform: translateY(100%);
    opacity: 0;
    background: $white;
    z-index: 50;
    transition: opacity .2s ease;
    will-change: opacity;
    display: none;
  }

  &__item {
    padding: 20px 10px;
    border-top: 1px solid $light_gray;
    transition: background .2s ease, color .2s ease;

    &:hover {
      background: $black;
      color: $white;
    }
  }

  &:hover {
    .dropdown__container {
      opacity: 1;
      display: block;
    }
  }
}

@media only screen and (max-width : 1024px) {
  .shop {
    .container {
      flex-wrap: wrap;
      justify-content: space-around;
    }

    .product-view {
      &__button {
        padding: 20px 10px;
      }

      &__action-item {
        width: 100%;

        &:nth-child(2n) {
          margin-left: 0;
        }

        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
  }
}

@media only screen and (max-width : 767px) {
  .shop {

    .container {
      flex-flow: column nowrap;
      align-items: center;
    }

    .quickshop-item {
      width: 85%;

      &:not(:first-child) {
        margin-top: 50px;
      }
    }
  }

  .product-view {
    padding: 30px 0 80px 0;

    &__close {
      bottom: -60px;
      right: 50%;
      transform: translate(50%);
    }

    &__inner {
      flex-flow: column nowrap;
    }

    &__row {
      width: 100%;

      &:not(:first-child) {
        margin-left: 0;
      }
    }

    &__title {
      margin-top: 30px;
    }

    &__consist {
      padding-right: 5%;
    }

    &__button {
      font-size: 14px;
    }
  }

  .dropdown {
    font-size: 14px;
    &__header {
      font-size: 14px;

      &::before {
        right: -5%;
      }
    }
  }
}
