.team {
  background: $light_gray;
  padding-top: 50px;
  margin-bottom: 50px;
  overflow: hidden;

  &__mobile-header {
    display: none;
    padding: 15px;
    background: #fff;
    font-size: 16px;
    text-align: center;
  }

  &__top {
    display: flex;
    flex-flow: row nowrap;
    min-height: 400px;
  }

  &__story {
    width: 30%;
    background: url(cup) no-repeat center / cover;
    color: $white;
    position: relative;
  }

  &__opacity {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: rgba(0,0,0,0.5);
    height: 100%;
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    text-align: center;
  }

  &__title {
    font-size: 24px;
    letter-spacing: .5px;
    text-transform: uppercase;
  }

  &__description {
    margin-top: 20px;
    font-size: 12px;
    letter-spacing: .5px;
  }

  &__button {
    margin-top: 20px;
    padding: 15px 30px;
    text-decoration: none;
    text-transform: uppercase;
    color: $black;
    background: $white;
    display: inline-block;
    font-size: 14px;
    letter-spacing: 1px;
    &:hover {
      background: rgba(255,255,255,0.6)
    }
  }

  &__image {
    width: 70%;
    background: url(banner) no-repeat center / cover;
  }

  &__bottom {
    display: flex;
    flex-flow: row nowrap;
    position: relative;
    max-height: 345px;
  }

  &__prev-inner {
    width: 50%;
    position: relative;
    overflow: hidden;
  }

  &__label {
    position: absolute;
    right: 4%;
    bottom: 4%;
    font-size: 24px;
    letter-spacing: 1px;
  }

  &__slider-inner {
    width: 50%;
    position: relative;
  }

  &__counter {
    position: absolute;
    bottom: 4%;
    right: 30%;
    display: flex;
    flex-flow: row nowrap;

    div {
      padding: 0 2px;
    }
  }

  &__slider {
    width: 100%;
    position: absolute;
    right: 0;
    top: -15%;


    &_prev {
      width: 100%;
      position: absolute;
      top: -15%;
      left: 0;
      opacity: .3;

      .slick-arrow {
        display: none !important;
      }
    }
  }

  &__image {
    min-height: 100px;

    img {
      display: block;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 50px);
      width: 120%;
    }
  }

  &__footer {
    margin-top: 80px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
  }

  &__info {
    font-size: 20px;
    line-height: 20px;
    padding: 0 10px;

    &:not(:first-child) {
      border-left: 2px solid $black;
    }
  }

  &__slide {
    position: relative;
  }

  &__slide-text {
    position: absolute;
    top: 13%;
    right: 0;
    background: $white;
    padding: 15px 20px;
    width: 55%;
  }

  &__slide-name {
    text-transform: uppercase;
  }

  &__slide-team {
    margin-top: 10px;
    font-size: 14px;
    text-transform: uppercase;
  }

  &__slide-position {
    font-size: 14px;
    margin-top: 2px;
  }

  &__slide-bio {
    font-size: 12px;
  }

  &__slide-image {
    width: 60%;
    height: 400px;
    overflow: hidden;
    position: relative;

    img {
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .slick-arrow {
    position: absolute;
    z-index: 200;
    top: 70%;
    transform: translateY(-50%);
    border: none;
    outline: none;
    font-size: 0;
    background: transparent;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      padding: 20px 25px;
      background: $black;
      color: $white;
      font-size: 16px;
      transition: background .2s ease, color .2s ease;
    }

    &:hover {
      &::before {
        background: $gray;
      }
    }
  }

  .slick-next {
    left: 60%;

    &::before {
      content: 'NEXT';
    }
  }

  .slick-prev {
    left: 1px;

    &::before {
      transform: translateX(-100%);
      content: 'PREV';
    }
  }
}

@media only screen and (max-width : 1024px) {
  .team {
    padding: 0;

    .container {
      padding: 0;
    }
  }
}

@media only screen and (max-width : 767px) {
  .team {
    padding-top: 0;
    margin-bottom: 10px;

    .container {
      padding: 0;
    }

    &__mobile-header {
      display: block;
    }

    &__title {
      display: none;
    }

    &_description {
      font-size: 14px;
      letter-spacing: .5px;
      line-height: 20px;
    }

    &__prev-inner {
      display: none;
    }

    &__slider {
      height: 400px;
    }

    &__top {
      flex-flow: column nowrap;
      height: 1px;
      min-height: 200px;
    }

    &__story {
      width: 100%;
      min-height: 100%;
    }

    &__image {
      height: 0%;
      width: 100%;
    }

    &__bottom {
      flex-flow: column nowrap;
      max-height: initial;
    }

    &__slider-inner {
      width: 100%;
    }

    &__slide {
      height: 340px !important ;
    }

    &__slide-image {
      width: 100%;
      height: 300px;

      img {
        max-width: 100%;
        height: initial;
        min-height: 100%;
      }
    }

    &__slide-text {
      bottom: 0;
      width: 90%;
      right: initial;
      left: 0;
      top: initial;
    }

    &__counter {
      right: 50%;
      transform: translateX(50%);
      bottom: 4.5%;
    }

    .slick-arrow {
      bottom: 10px;
      top: initial;
      padding: 21px 25px;
      background: $black;
      bottom: 0px;
      transform: translateY(0%);

      &::before {
        width: 100%;
        bottom: 0;
        top: initial;
        position: initial;
        padding: 0;
        background: transparent;
      }

      &:hover {
          &::before {
            background: transparent;
          }
      }
    }

    .slick-next {
      right: 0;
      left: initial;
    }
  }
}
