.manage {
  min-height: calc(100vh - 385px);
  padding: 10px 0 40px 0;

  &__title {
    font-size: 30px;
    line-height: 30px;
    text-transform: uppercase;
    margin-top: 30px;
  }

  &__inner {
    margin-top: 40px;
  }
}

.manage-table {

  &__row {
    display: flex;
    flex-flow: row nowrap;
    padding: 10px 0;

    &:not(:last-child) {
      border-bottom: 1px solid $light_gray;
    }

    &_head {
      color: $dark_gray;
      text-transform: uppercase;
      font-size: 14px;
      letter-spacing: .5px;
    }
  }

  &__row-item {
    flex: 1 1 16%;
    padding-right: 15px;

    &_actions {
      display: flex;
      flex-flow: row nowrap;
      color: $red;
    }
  }

  &__action {
    padding: 0 10px;
    cursor: pointer;

    &:first-child {
      padding-left: 0;
    }

    &:not(:first-child) {
      border-left: 1px solid $gray;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &__mobile-label {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .manage-table {
    &__row-item {
      padding-right: 5px;
    }
  }
}

@media screen and (max-width: 767px) {
  .manage {
    &__title {
      font-size: 20px;
      line-height: 20px;
      margin-top: 50px;
      text-align: center;
    }

    &__inner {
      margin-top: 20px;
    }
  }

  .manage-table {
    &__row {
      flex-flow: column nowrap;

      &_head {
        display: none;

      }
    }

    &__row-item {
      font-size: 14px;

      &:not(:first-child) {
        margin-top: 10px;
      }
    }

    &__mobile-label {
      display: inline-block;
      margin-right: 10px;
      color: $dark_gray;
      text-transform: uppercase;
      font-size: 14px;
      letter-spacing: .5px;
    }
  }
}
