.about-content {
  padding-bottom: 40px;

  &__inner {
    max-width: 960px;
    margin: 0 auto;

    img {
      width: 100%;
      margin-top: 30px;
      display: block;
    }

    h1 {
      padding: 0 13%;
      font-size: 28px;
      font-weight: 400;
    }

    h2 {
      padding: 0 13%;
      margin-top: 25px;
      font-weight: 400;
      font-size: 20px;
      margin-bottom: 25px;
    }

    p {
      padding: 0 13%;
      font-size: 16px;
    }

    .image-caption {
      color: $gray;
      text-align: center;
      display: block;
      font-weight: 300;
      font-size: 14px;
      margin-top: 10px;
      margin-bottom: 40px;
    }
  }
}

@media only screen and (max-width : 767px) {
  .about-content {
    &__inner {
      h1,
      h2,
      p {
        padding: 0 5%;
      }

      h1 {
        font-size: 22px;
      }

      h2 {
        font-size: 18px;
      }
    }
  }
}
