.mobile-menu {
  position: fixed;
  height: calc(100vh - 42px);
  top: 42px;
  width: 100%;
  left: 0;
  z-index: 1000;
  background: $white;
  padding: 10px;
  opacity: 1;
  transition: transform .2s ease, opacity .2s ease;
  will-change: transform, opacity;
  transform: translate3d(0,0,0);

  &._hidden {
    transform: translate3d(-100%, 0, 0);
    opacity: 0;
  }

  &__nav {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;

    a {
      font-size: 26px;
      color: $black;
      text-decoration: none;
      text-align: center;
      display: block;

      span {
        display: block;
      }
    }
  }

  &__link {
    &:not(:first-child) {
      margin-top: 15px;
    }
  }

  &__footer {
    position: absolute;
    bottom: 5%;
    width: 100%;
    left: 5%;
  }

  &__row {
    display: flex;
    flex-flow: row nowrap;
    text-align: center;

    .snipcart-user-profile {
      display: flex;
      flex-flow: row nowrap;
    }

    .snipcart-user-email {
      margin-left: 10px;
    }

    .snipcart-summary {
      margin-left: 10px;
    }

    a {
      font-size: 16px;
      text-decoration: none;
      color: $black;
    }

    &:last-child {
      margin-top: 15px;
    }


  }
}
