.header {
  padding: 10px 0;

  .container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  &__menu-icon {
    position: absolute;
    display: none;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background: url(bars) no-repeat center / contain;
  }

  &__logo {
    flex: 1 1 40%;

    img {
      max-width: 70px;
      display: block;
    }
  }

  &__back-button {
    text-transform: uppercase;
    a {
      color: $gray;
      text-decoration: none;
      display: flex;
      flex-flow: row nowrap;
      transition: color .2s ease;

      &::before {
        content: '<';
        display: inline-block;
        margin-right: 5px;
        vertical-align: middle;
        line-height: 14px;
      }
    }

    &:hover {

      a,
      .header__title {
        color: $black;
      }
    }
  }

  &__title {
    font-size: 22px;
    line-height: 22px;
    text-decoration: none;
    color: $black;

    span {
      font-size: 12px;
      vertical-align: top;
      line-height: 12px;
    }

    &_wholesale {
      color: $gray;
      margin-left: 15px;
      transition: color .2s ease;
    }
  }

  &__nav {
    flex: 1 1 40%;
    text-transform: uppercase;
    text-align: right;

    &_wholesale {
      flex: 0 0 40%;
    }
  }

  &__links {
    padding: 10px 0;
    display: inline-block;
    min-width: 55%;

    &:not(:last-child) {
      border-bottom: 1px solid $gray;
    }
  }

  &__link {
    display: inline-block;
    font-size: 14px;

    &:not(:first-child) {
      margin-left: 10px;
    }

    &_login {
      margin-right: 15px;

      a {
        padding: 0 5px;

        &:not(:first-child) {
          border-left: 1px solid $gray;
        }
      }
    }

    a {
      color: $gray;
      text-decoration: none;
      display: inline-block;
      transition: color .2s ease;

      &:hover {
        color: $black;
      }
    }
  }

  .snipcart-summary {
    display: inline-block;
    margin-left: 10px;
  }

  .snipcart-user-email {
    margin-left: 10px;
  }
}

@media only screen and (max-width : 767px) {

  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: $white;
    z-index: 1000;

    .container {
      justify-content: center;
    }

    &__menu-icon {
      display: block;
    }

    &__nav,
    &__logo {
      display: none;
    }

    &__back-button {
      margin-right: 35px;
    }

    &__title {
      text-align: center;
      white-space: nowrap;
      margin-left: 10px !important;

    }

  }
}

@media only screen and (max-width : 1024px) {
  .header {
    &__links {
      max-width: 300px;
    }

    &__link_login {
      margin-right: 0;

      a {
        padding: 0;
      }
    }

    &__nav {
      padding-left: 5%;
    }

    &__title {
      white-space: nowrap;
      margin-left: 20px;
    }

    &__logo {
      flex: initial;
    }
  }
}
