.about-banner {
  height: calc(100vh - 97px);
  color: $white;
  position: relative;

  &__opacity {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  &__text {
    font-size: 37px;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 30px;
  }

  &__bottom-block {
    position: absolute;
    width: 100%;
    max-width: 1000px;
    bottom: 0;
    background: $white;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;
  }
}

@media only screen and (max-width : 1024px) {
  .about-banner__bottom-block {
    bottom: -1px;
  }
}

@media only screen and (max-width : 767px) {
  .about-banner {
    &__bottom-block {
      bottom: -1px;
      width: 95%;
    }
    &__content {
      width: 80%;
    }
  }
}
